.footer {
  background-color: #333;
  color: #fff;
  padding: 2rem;
  text-align: center;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  align-items: center;
  justify-content: center;
}

.footer-contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #444;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}

.footer-contact p {
  margin: 0.5rem 0;
  font-size: 1rem;
}

.footer-contact a {
  color: #4da8da;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-contact a:hover {
  color: #1b6fa5;
}

/* Responsive Design */
@media (max-width: 768px) {
  .footer-container {
    grid-template-columns: 1fr;
    padding: 1.5rem;
  }

  .footer-contact p {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .footer-container {
    padding: 1rem;
  }

  .footer-contact p {
    font-size: 0.8rem;
  }
}
