
.navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #333;
  margin-left: -20px;

  font-family: Arial, sans-serif;
}

.nav-link {
  color: #fff;
  text-decoration: none;
  padding: 10px 0px;
  transition: background-color 0.3s;
}

.nav-link:hover {
  background-color: #555;
  border-radius: 4px;
}




@media (min-width: 360px) and (max-width: 650px) {



  .navbar {
    display: flex;
