.projects-container {
  padding: 2rem;
  background-color: #f9f9f9;
}

.projects-section {
  max-width: 1200px;
  margin: 0 auto;
}

.projects-list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.project-item {
  background-color: #ffffff;
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.project-item h3 {
  margin-bottom: 1rem;
  font-size: 1.5rem;
  color: #333;
}

.project-item p {
  margin-bottom: 1rem;
  font-size: 1.125rem;
  line-height: 1.6;
  color: #555;
}

.project-link {
  color: #007bff;
  text-decoration: none;
  font-weight: bold;
}

.project-link:hover {
  text-decoration: underline;
}

/* Responsive Design */
@media (max-width: 768px) {
  .projects-list {
    gap: 1.5rem;
  }
}

@media (max-width: 480px) {
  .project-item h3 {
    font-size: 1.25rem;
  }

  .project-item p {
    font-size: 1rem;
  }
}
